import { h, a, br, button, div, em, h1, input, p, span } from '@cycle/dom'

const listOfJosa =
  [ '은', '는', '이', '가', '을', '를', '와', '과', '이어', '여', '으로', '로', '이에요', '예요', '이었', '였', '아', '야', '이?' ]

const renderHeader = () =>
  div('.Header', [
    div('.Header-nav', [
      h1('.Header-title', 'josa 테스트 REPL'),
      div('.Header-links', [
        a('.Header-link', {
          props: { href: 'https://github.com/kimdhoe/josa' }
        }, [
          span('josa'),
          span('GitHub')
        ]),
        a('.Header-link', {
          props: { href: 'https://npmjs.com/package/josa' }
        }, [
          span('josa'),
          span('npm')
        ])
      ]),
      p('.Header-description', [
        'josa는 한글 조사를 동적으로 결정해주는 라이브러리입니다.',
        br(),
        '이 페이지에서 josa v2.0.1을 시험해볼 수 있습니다.'
      ])
    ]),
    p('.Header-josaList', [
      div('.Header-josas', listOfJosa.map(j => span('.Header-josa', [ j ])))
    ])
  ])

const renderOutput = ({ subject, result: { text, failed } }) =>
  div(`.Segments${failed ? '.is-error' : ''}`, [
    div('.Segments-segment', [ subject ]),
    div(`.Segments-segment`, [ text ])
  ])

const renderOutputs = outputs =>
  div('.Outputs', [
    div('.Segments.example', [
      div('.Segments-segment', [
        '> 친구#{이} 선생님#{와} 함께 학교#{으로} 갑니다.'
      ]),
      div('.Segments-segment', [
        '친구가 선생님과 함께 학교로 갑니다.'
      ])
    ]),
    ...outputs.map(renderOutput)
  ])

const renderPrompt = (value, shouldFocus) =>
  div( '.Prompt'
     , [ span('.Prompt-symbol', [ '> ' ])
       , input( '.Prompt-input'
              , { props: { type: 'text', value }
                , hook:  { insert: vnode => { vnode.elm.focus() } }
                }
              )
       ]

     )

const view = state$ =>
  state$.map(({ text, current, outputs, shouldFocus }) =>
    div('.container', [
      renderHeader(),
      div('.Repl', [
        renderOutputs(outputs),
        renderPrompt(text, shouldFocus)
      ])
    ])
  )

export default view



